
import Search from 'vux/src/components/search/index.vue'
import Scroller from 'vux/src/components/scroller/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Divider from 'vux/src/components/divider/index.vue'
import Loading from 'vux/src/components/loading/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'


export default {
directives: {
TransferDom
},
components: {
Search,
Scroller,
XHeader,
Divider,
Loading
},
data () {
return {
typelist: [],
loading: false,
text: '正在加载',
loading: false,
msgContent: [],
isActive: 1,
searchValue: '',
tab: 0,
type: this.$route.query.type,
typename: '所属分类',
haoping: 0,
price: 0,
page: 1,
list: [],
}
},
mounted () {
this.getCat()
let search = document.getElementById('search').offsetHeight
let footer = document.getElementById('footer').offsetHeight

window.msgContent = document.documentElement.clientHeight
this.msgContent = (window.msgContent - footer - search - 50) + 'px'

window.onresize = () => {
return (() => {
window.msgContent = document.documentElement.clientHeight
this.msgContent = (window.msgContent - footer - search - 50) + 'px'
})()
}

this.getTeacher();
},
methods: {
submit(){
let that = this;
that.list = []
layui.use('flow', function(){
let flow = layui.flow;
let $ = layui.jquery
,element = layui.element;
flow.load({
elem: '#all' 
,isAuto:true
,scrollElem: '#all' //滚动条所在元素，一般不用填，此处只是演示需要。
,loadMore:'#loadMore'
,done: function(page, next){
that.$api.teacher({params: {
'search': that.searchValue,
}}).then(res => {
that.loading = false
that.list = that.list.concat(res.data.results)
next('', page < Math.ceil(res.data.count / 10 ));
})
}
});
});
},
getTeacher (){
let that = this;
layui.use('flow', function(){
let flow = layui.flow;
let $ = layui.jquery
,element = layui.element;

flow.load({
elem: '#all' 
,isAuto:true
,scrollElem: '#all' //滚动条所在元素，一般不用填，此处只是演示需要。
,loadMore:'#loadMore'
,done: function(page, next){
that.$api.teacher({params: {
'type': that.type,
'page': page
}}).then(res => {
that.loading = false
that.list = that.list.concat(res.data.results)
next('', page < Math.ceil(res.data.count / 10 ));
})
}
});

});
},
refresh () {
let that = this;
that.list = [];
layui.use('flow', function(){
let flow = layui.flow;
let $ = layui.jquery
,element = layui.element;
flow.load({
elem: '#all'
,isAuto:true
,scrollElem: '#all'
,loadMore:'#loadMore'
,done: function(page, next){
that.$api.teacher({params: {
'type': that.type,
'page': page}}).then(res => {
that.loading = false
that.list = that.list.concat(res.data.results)
next('', page < Math.ceil(res.data.count / 10 ));
})
}
});

});
},
sethaoping(){
let that = this;
that.list = [];
layui.use('flow', function(){
let flow = layui.flow;
let $ = layui.jquery
,element = layui.element;

flow.load({
elem: '#all' 
,isAuto:true
,scrollElem: '#all' //滚动条所在元素，一般不用填，此处只是演示需要。
,loadMore:'#loadMore'
,done: function(page, next){

that.haoping == 1?that.haoping = 0:that.haoping = 1
that.price = 0
that.list = [];

if(that.haoping == 1){
that.teacherOrder(data => {
that.loading = false
that.list = that.list.concat(data.results)
next('', page < Math.ceil(data.count / 10 ));
},'-haopinglv')
}else{
that.teacherOrder(data => {
that.loading = false
that.list = that.list.concat(data.results)
next('', page < Math.ceil(data.count / 10 ));
},'haopinglv')
}

}
});

});
},
setprice(){
let that = this;
that.list = [];
layui.use('flow', function(){
let flow = layui.flow;
let $ = layui.jquery
,element = layui.element;

flow.load({
elem: '#all' 
,isAuto:true
,scrollElem: '#all' //滚动条所在元素，一般不用填，此处只是演示需要。
,loadMore:'#loadMore'
,done: function(page, next){

that.price == 1?that.price = 0:that.price = 1
that.haoping = 0
that.list = [];

if(that.price == 1){
that.teacherOrder(data => {
that.loading = false
that.list = that.list.concat(data.results)
next('', page < Math.ceil(res.data.results.length / 10 ));
},'-price')
}else{
that.teacherOrder(data => {
that.loading = false
that.list = that.list.concat(data.results)
next('', page < Math.ceil(data.count / 10 ));
},'price')
}


}
});

});

},
selectType(id,name){
this.type = id
this.typename = name
this.refresh()
this.searchtype(0)
},
searchtype (id) {
this.tab = id
},
getTeachertest (data) {
this.loading = true
return this.$api.teacher({params: {
'type': this.type,
'page': this.page}}).then(res => {
data(res.data)
this.loading = false
})
},
teacherOrder (data,ordering) {
this.loading = true
this.page = 1
return this.$api.teacher({params: {
'type': this.type,
'page': this.page,
'ordering': ordering
}}).then(res => {
data(res.data)
this.loading = false
})
},

getCat: function () {
return this.$api.catogary().then(res => {
let result = res.data;
let typelist = [];
for(var i=0;i<result.length;i++){
typelist.push({key: result[i].id, value: result[i].title})
}
this.typelist = typelist
this.typename = result[this.type-1].title
if(result){
this.id = result.id
}
})
},
}
}



